<template>
  <div>
    <div class="form User-form plr">
      <div class="form-row" :class="clas.name">
        <label for="name">Name</label>
        <input type="text" v-model="name" id="name" @change="userHandle('name')">
      </div>
      <div class="form-row" :class="clas.email">
        <label for="email">Email</label>
        <input type="email" v-model="email" id="email" @change="userHandle('email')">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'User',
  data () {
    return {
      name: this.$store.state.user.name,
      email: this.$store.state.user.email,
      clas: {
        name: '',
        email: ''
      }
    }
  },
  computed: {
    version: function () {
      return this.$store.state.user.version
    }
  },
  methods: {
    userHandle (field, event) {
      this.clas[field] = 'loading'
      this.$store.dispatch('setPlayer', {type: field, items: this[field]})
    },
    watchVersion () {
      this.clas.name = ''
      this.clas.email = ''
    }
  },
  watch: {
    version () {
      this.watchVersion()
    }
  }
}
</script>
<style lang="sass" scoped>
  .User-form
    padding-top: 1em;

  .loading
    position: relative;

  .loading input
    color: #ccc;
    background: #f0f0f0;

  .loading:after
    content: '';
    position: absolute;
    width: 2em;
    height: 2em;
    right: 0;
    bottom: 0;
    border-radius: 300%;
    background: #000;
    opacity: 0;
    animation: loading 1s infinite;

  @keyframes loading
    0%, 100%
      transform: scale(.3);
      opacity: .5;
    50%
      transform: scale(.1);
      opacity: .1;

</style>



